import { css } from "emotion";
import { useState, useEffect } from 'react';
import { Wrapper, Card, Icon } from "reactism/elements";
import { LoadingDetail } from "reactism/components";
import Link from "next/link";
import { Color } from "reactism/utils";
import { withRouter } from 'next/router';

// Redux
import { connect } from 'react-redux'
import { getDetailNews } from 'reactism/actions/news'

const ItemDetail = ({ getDetailNews, router }) => {
  const [ data, setData ] = useState({});

  useEffect(() => {
    getDetailNews(router.query.id).then(response => setData(response))
  }, [])

  useEffect(() => {
    setData({})
    getDetailNews(router.query.id).then(response => setData(response))
  }, [router])

  const wrapper = css`
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 15vh 70vh 1fr;

    .next,
    .prev {
      position: absolute;
      bottom: -3em;
      a {
        text-decoration: none;
        color: #000;
        transition: 0.3s ease;
        padding: 10px;
        &:hover {
          background-color: ${Color.success};
          border-radius: 5px;
          color: #fff;
          box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
        }
      }
    }
    .prev {
      left: 0;
      i {
        margin-right: 10px;
      }
    }
    .next {
      right: 0;
      i {
        margin-left: 10px;
      }
    }
    .titleDetail {
      display: grid;
      grid-template-columns: 2fr 1fr;
      span {
        justify-self: end;
        color: rgba(0, 0, 0, 0.5);
      }
      h2 {
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
      }
      padding: 0 2em;
    }
    .imgBox {
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    .content {
      display: grid;
      grid-template-columns: 1fr 3fr 1fr;
      grid-template-areas: ". col1 col2";
      justify-content: center;
      padding-top: 1em;
      div:nth-child(1) {
        grid-area: col1;
      }
      div.social {
        padding-top: 3em;
        grid-area: col2;
        text-align: center;
        img {
          margin: auto;
          display: block;
          width: 15%;
          margin-bottom: 1.5em;
        }
      }
    }

    @media (max-width: 700px) {
      grid-template-rows: 20vh 35vh 1fr;
      .titleDetail {
        grid-template-columns: 1fr;
        h2 {
          font-size: 1em;
          margin: 0;
          margin-top: 10px;
        }
        span {
          justify-self: start;
        }
      }

      div.content {
        div.social {
          img {
            display: inline-block;
            width: 12%;
            margin: 10px;
          }
        }
      }
    }
  `;

  return (
    <Wrapper pl={7} pr={7}>
      {
        Object.keys(data).length > 0
      ?
      <Card shadow="true">
        <div className={wrapper}>     
          <div className="titleDetail">
            <h2>{ data[0].title}</h2>
            <span>
              <p>{ data[0].created_at }</p>
            </span>
          </div>
          <div className="imgBox">
            <img src={ data[0].image.image_url } alt="" />
          </div>
          <div className="content">
            <div>
              <p>{ data[0].desc }</p>
            </div>

            <div className="social">
              <img src="/static/icon/ic_fb.svg" alt="iconfb" />
              <img src="/static/icon/ic_twitter.svg" alt="icontwitter" />
              <img src="/static/icon/ic_linkedin.svg" alt="iconlinked" />
            </div>
          </div>

          {/* <div className="prev">
            <Link href="/berita/detail">
              <a>
                <Icon name="arrow_back" />
                SEBELUMNYA
              </a>
            </Link>
          </div>

          <div className="next">
            <Link href="/berita/detail">
              <a>
                SELANJUTNYA
                <Icon name="arrow_forward" />
              </a>
            </Link>
          </div> */}
        </div>
      </Card>
        :
          <LoadingDetail />
      }
    </Wrapper>
  );
};

const mapStateToProps = state => ({
  detail: state.news.detail
})


export default withRouter(connect(mapStateToProps, {
  getDetailNews
})(ItemDetail));
