import { Component } from "react";
import ItemDetail from "./partials/itemDetail";
import { css } from "emotion";
import { Title, Icon } from "reactism/elements";
import { Link } from "Routes";
import { Color } from "reactism/utils";

class Index extends Component {
  render() {
    const baseStyle = css`
      background-color: #f6f6f6;
      display: grid;
      justify-items: center;
      /* position: relative; */
      div.title {
        padding: 1rem 0 1.5rem 0;
        justify-self: center;
        margin-left: -5.5rem;
      }

      div.back {
        position: absolute;
        top: 8em;
        left: 6em;
        a {
          color: #000;
          i {
            font-size: 2.5em;
          }
          &:hover {
            color: ${Color.success};
          }
        }
      }

      padding-bottom: 5rem;
      box-shadow: border-box;

      @media (max-width: 700px) {
        div.back {
          top: 5em;
          left: 1em;
        }
        div.content {
          grid-template-columns: 1fr;
          padding: 1.5em;
          grid-template-areas:
            "col1"
            "col2";
        }

        .next,
        .prev {
          bottom: -70em;
        }
        .prev {
          left: 10px;
          i {
            margin-right: 10px;
          }
        }
        .next {
          right: 10px;
          i {
            margin-left: 10px;
          }
        }
      }
    `;

    return (
      <div className={baseStyle}>
        <div className="back">
          <Link route="kegiatan">
            <a>
              <Icon name="arrow_back" />
            </a>
          </Link>
        </div>
        <div className="title">
          <Title left={5}>Berita</Title>
        </div>
        <ItemDetail />
      </div>
    );
  }
}

export default Index;
